import { Component, OnInit, HostListener } from '@angular/core';
import { Validators, FormGroup, FormBuilder } from '@angular/forms';
import { AppLoaderService } from '../../core/components/app-loader/app-loader.service';
import { SignInService } from '../sign-in.service';
import { Router } from '@angular/router';
import { _PRule } from '../../password-strength/interface/password-rule';
import { MassageMasterService } from '../../massage-master.service';

@Component({
  selector: 'app-new-password',
  templateUrl: './new-password.component.html',
  styleUrls: ['./new-password.component.scss']
})
export class NewPasswordComponent implements OnInit {

  public _pRule: _PRule = {
    at_least_x_chars: 8,
    at_max_x_chars: 30,
    at_least_one_lower_case_char: true,
    at_least_one_upper_case_char: false,
    at_least_one_digit_char: true,
    at_least_one_special_char: true
  };
  public newPasswordForm: FormGroup;
  public passwordObj: any = {
    passworCheck: false,
    password: ''
  };
  public showModule: boolean = false;
  public newPasswordJson: any = {};
  public passwordChangedJson: any = {};
  public isNewPassword: boolean = true;
  public emailId: string = '';

  constructor(private appLoaderService: AppLoaderService,
    private signInService: SignInService,
    private _router: Router, private massageMasterService: MassageMasterService,
    private _fb: FormBuilder
  ) { }

  ngOnInit() {
    this.signInService.currentId.subscribe(id => {
      this.appLoaderService.hideLoader();
      if (id !== null) {
        this.emailId = id;
        this.getNewPasswordRecovery();
      } else {
        this._router.navigate(['authentication/signIn']);
      }
    });
  }

  private getNewPasswordRecovery(): void {
    this.newPasswordJson = this.massageMasterService.getMessageJsonData();
    this.showModule = true;
    this.newPasswordForm = this._fb.group({
      password: ['', Validators.required],
      email: [this.emailId]
    });
  }

  public validatedPassword(password: object): void {
    console.log(password);
    this.passwordObj = password;
    if (password['passworCheck']) {
      this.newPasswordForm.controls.password.setValue(
        this.passwordObj.password);
    }
  }

  public submit(): void {
    this.isNewPassword = false;
    const obj = {
      emailId: this.newPasswordForm.value.email,
      password: this.newPasswordForm.value.password
    };

    this.signInService.newPasswordApi(obj).subscribe(result => {
      console.log('result', result);
      this.appLoaderService.hideLoader();
      const data = result['data'];
      if (data.status.toLowerCase() === 'success') {
      } else {
        this.massageMasterService.showError(this.massageMasterService.getErrors().default);
      }
    }, (err) => {
      console.log('error', err);
      this.appLoaderService.hideLoader();
      const errorObj = JSON.parse(err.completeError.response);
      const msg = errorObj.message ? errorObj.message : this.massageMasterService.getErrors().default;
      this.massageMasterService.showError(msg)
    });
  }

  public login(): void {
    this._router.navigate(['authentication/signIn']);
  }

}
