import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SignInComponent } from './sign-in/sign-in.component';
import { Routes, RouterModule } from '@angular/router';
import { ReactiveFormsModule, FormsModule } from '@angular/forms';
import { MaterialModule } from '../material/material.module';
import { FlexLayoutModule } from '@angular/flex-layout';
import { PasswordRecoveryComponent } from './password-recovery/password-recovery.component';
import { NewPasswordComponent } from './new-password/new-password.component';
import { PasswordStrengthModule } from '../password-strength/password-strength.module';
import { CoreModule } from '../core/core.module';
import { NgOtpInputModule } from 'ng-otp-input';

const routes: Routes = [
  {
    path: '',
    children: [
      { path: '', redirectTo: 'sign-in', pathMatch: 'full' },
      { path: 'sign-in', component: SignInComponent },
      { path: 'password-recovery', component: PasswordRecoveryComponent },
      { path: 'new-password', component: NewPasswordComponent },
    ]
  }
];


@NgModule({
  declarations: [SignInComponent, PasswordRecoveryComponent, NewPasswordComponent],
  imports: [
    CommonModule,NgOtpInputModule,
    MaterialModule,
    ReactiveFormsModule,
    FormsModule, PasswordStrengthModule,
    FlexLayoutModule, CoreModule,
    RouterModule.forChild(routes),
  ],
})

export class SignInModule { }
